.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.text-right{
  text-align: right;
}
.nav{
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 20px;
}
nav .nav-left, .nav-right{
  margin: 2px 20px;
}
nav .nav-left a{
  font-size: 23px;
  color: grey;
}
nav a{
  color: black;
  text-decoration: none;
  font-size: 20px;
}
.buttom{
  display: flex;
  justify-content: end;
  margin: 20px !important;
}
.task-container{
  border: 2px solid rgba(231, 212, 212);
  margin: 8px;
}
.task-container .taskmeta{
  display: flex;
}
.task_edit form {
  display: flex;
  flex-direction: column;
}
.task_edit form input, label, button{
  margin: 5px 10px !important;
}
